                    "body": "Hi, Andrew-\n\nI'm the founder of Inkling, a company that has just recently launched its plans for an iPad-based learning platform. We're based in San Francisco, and our website is at www.inkling.com.\n\nWe are always on the lookout for talented developers, and you've got a very interesting profile. (And you're Canadian, like me!)\n\nLooks like you are still in school, but I'm not sure whether your site is up to date. What's your status? Any interest in considering a gig in California?\n"
