            "description": "Over the course of about 10 months we ran over 20 experiments of varying complexity.\n\nHowever, around this time was when we disbanded the Growth Team since, while we did make some great steps forward, we weren't making enough progress. From the Growth Team I ended up switching over to work on Habitat.\n",
