            "type": "sad",
            "description": "This was hard.\n\nI remember seeing an email from Matt the day before asking everyone to come in by 10:45am the next day. Everyone was somewhat excited since the email was vague and mysterious. Half the company was asked to meet down on the 4th floor where Matt told them they had been let go, while the other half stayed on the 5th where Rob let us know what going on.\n\nIt was super difficult seeing so many people that I'd worked with since the early days leave. The next few months were pretty rough too as people trickled out on their own. Eventually though, we got our footing again and things started to improve. Looking back, I'm glad I stuck it out.\n",
